import React from "react";

const Home = () => {
  return (
    <>
      <section className="home">
        <div className="container">
          <div className="home__wrapper">
            <div className="home__timer">
              <div className=" flex flex-col items-center ml-2">
                <p className="text-gray-300 uppercase text-sm">
                  Time left until lunching
                </p>
                <div
                  className="flex items-center justify-center space-x-4 mt-4"
                  x-data="timer(new Date().setDate(new Date().getDate() + 1))"
                  x-init="init();"
                >
                  <div className="flex flex-col items-center px-4">
                    <span
                      x-text="time().days"
                      className="text-4xl lg:text-5xl text-gray-100"
                    >
                      00
                    </span>
                    <span className="text-gray-800 mt-2">Days</span>
                  </div>
                  <span className="w-[1px] h-24 bg-gray-800"></span>
                  <div className="flex flex-col items-center px-4">
                    <span
                      x-text="time().hours"
                      className="text-4xl lg:text-5xl text-gray-100"
                    >
                      23
                    </span>
                    <span className="text-gray-800 mt-2">Hours</span>
                  </div>
                  <span className="w-[1px] h-24 bg-gray-800"></span>
                  <div className="flex flex-col items-center px-4">
                    <span
                      x-text="time().minutes"
                      className="text-4xl lg:text-5xl text-gray-100"
                    >
                      59
                    </span>
                    <span className="text-gray-800 mt-2">Minutes</span>
                  </div>
                  <span className="w-[1px] h-24 bg-gray-800"></span>
                  <div className="flex flex-col items-center px-4">
                    <span
                      x-text="time().seconds"
                      className="text-4xl lg:text-5xl text-gray-100"
                    >
                      28
                    </span>
                    <span className="text-gray-800 mt-2">Seconds</span>
                  </div>
                </div>
              </div>
              <button
                target="_blank"
                className="group relative overflow-hidden bg-gray-200 focus:ring-4 focus:ring-gray-300 inline-flex items-center px-7 py-2.5 rounded-lg text-white justify-center mt-5"
              >
                <span className="z-40 text-gray-800">Start</span>
                <svg
                  className="z-40 ml-2 -mr-1 w-3 h-3 transition-all duration-300 group-hover:translate-x-1 text-gray-800"
                  fillRule="currentColor"
                  viewBox="0 0 20 20"
                  // xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fillRule="evenodd"
                    d="M10.293 3.293a1 1 0 011.414 0l6 6a1 1 0 010 1.414l-6 6a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-4.293-4.293a1 1 0 010-1.414z"
                    clipRule="evenodd"
                  ></path>
                </svg>
                <div className="absolute inset-0 h-[200%] w-[300%] rotate-45 translate-x-[-70%] transition-all group-hover:scale-100 bg-white/50 group-hover:translate-x-[50%] z-20 duration-1000"></div>
              </button>

            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Home;
